    <!--    <button @click="$store.commit('setName', 'park')">ÎÇòÎäî parkÏù∏Îç∞?</button>-->
    <!--  mapMutations ÌôúÏö©Ïãú ÏïÑÎûòÏôÄ Í∞ôÏù¥ Î≥∏Ïù∏Ïùò Ìï®ÏàòÏù∏ Í≤É Í∞ôÏùÄ Î∞©ÏãùÏúºÎ°ú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÏùå  -->
<!--    <button @click="setName('park')">ÎÇòÎäî parkÏù∏Îç∞?</button>-->
