<!--  <p>{{ now() }} <span>{{ counter }}</span> <button @click="counter ++">Ïû¨Ïã§Ìñâ</button></p>-->

  <Container :step="step" :img-url="imgUrl" @textData="getTextData"/>

  <!--  // axiosÎ°ú Î∂àÎü¨Ïò§Îäî Î∞©Î≤ï -->
  <!--  <button @click="moreData" class="btn-more">ÎçîÎ≥¥Í∏∞</button>-->
  <button @click="$store.dispatch('getData')" class="btn-more">ÎçîÎ≥¥Í∏∞</button>


