    <ul class="footer-button-plus">
      <!--   Ïó¨Îü¨ ÌååÏùºÏùÑ ÌïúÎ≤àÏóê ÌïòÍ≥† Ïã∂ÏùÑ Í≤ΩÏö∞ multiple   -->
      <!--   accept="image/*" Ïù¥ÎØ∏ÏßÄ ÌååÏùºÎßå Î≥¥Ïó¨Ï§Ñ Ïàò ÏûàÍ≤å ÎßåÎì§Ïñ¥Ï£ºÎäî Í≤É   -->
      <input @change="upload" multiple accept="image/*" type="file" id="file" class="inputfile"/>
