      imgUrl: '',
      textData: '',
      // tabActive: 0,
      // tabContents: [
      //   {
      //     id: 0,
      //     content: 'Tab UI ÎÇ¥Ïö© 1'
      //   },
      //   {
      //     id: 1,
      //     content: 'Tab UI ÎÇ¥Ïö© 2'
      //   },
      //   {
      //     id: 2,
      //     content: 'Tab UI ÎÇ¥Ïö© 3'
      //   },
      // ],
      filterData: '',
    }
  },
  created() {
  },
  methods: {
    // vuex mutations ÌïúÎ≤àÏóê Í∫ºÎÇ¥Ïì∞Îäî Î∞©Î≤ï
    ...mapMutations(['setMore', 'clkLike', 'setName']),
    moreData() {
      // this. Î•º Ïì∞Î†§Î©¥ ÌôîÏÇ¥Ìëú Ìï®ÏàòÎ•º Ïç®ÏïºÎê® ÏùºÎ∞ò Ìï®ÏàòÎäî thisÏùò ÏùòÎØ∏Î•º Ïû¨Ï†ïÏùòÌï®
      axios.get(`https://codingapple1.github.io/vue/more${this.count}.json`).then((result) => {
        // ÏöîÏ≤≠ ÏÑ±Í≥µ Ïãú Ïã§ÌñâÌï† ÏΩîÎìú
        // urlÏóêÏÑú Îì§Ïñ¥Ïò§Îäî Î™®Îì† Îç∞Ïù¥ÌÑ∞Í∞Ä Array ÌòïÏãùÏúºÎ°ú Îì§Ïñ¥ÏôÄÏÑú .data Î•º Ìï¥Ïïº ÏïàÏóê Îç∞Ïù¥ÌÑ∞Ïóê Ï†ëÍ∑ºÌï† Ïàò ÏûàÏùå
        // console.log(result.data);
        // ÌòÑÏ†ú Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§Ïóê Ï∂îÍ∞Ä
        this.contents.push(result.data);
        this.count++
      })
      // postÏöîÏ≤≠
      // postÏöîÏ≤≠ Î∞©Î≤ï .thenÏùÄ ÏÑ±Í≥µÌñàÏùÑ Îïå, catchÎäî Ïã§Ìå®Ïãú, errÎäî errÎç∞Ïù¥ÌÑ∞
      // axios.post('URL', {name: 'kim'}).then().catch((err) => {
      //   err
      // })
    },
    // tabActivate (idx) {
    //   this.tabActive = idx;
    // }
    upload(e) {
      // ÏóÖÎ°úÎìúÌïú Ïù¥ÎØ∏ÏßÄÎ•º Î∞îÎ°ú Î≥º Ïàò ÏûàÍ≤å ÎßåÎì§Ïñ¥Ï£ºÎäî ÎÇ¥Ïû•Ìï®Ïàò
      // eÎäî Ïù¥Î≤§Ìä∏ÏôÄ Í¥ÄÎ†®Îêú Ï†ïÎ≥¥Í∞Ä Ï†ÄÏû•Îêú ÌååÎùºÎØ∏ÌÑ∞
      let file = e.target.files;
      // console.log(file[0]);
      // Îã§Ïùå ÌéòÏù¥ÏßÄÎ°ú Ïù¥ÎèôÌïòÍ∏∞
      this.step++;
      // FileReader() ÌååÏùºÏùÑ Í∏ÄÏûêÎ°ú Î≥ÄÌôòÌï¥ÏÑú Ïù¥ÎØ∏ÏßÄ srcÏóê ÎÑ£ÏùÑ Ïàò ÏûàÍ≤å ÎèÑÏôÄÏ£ºÎäî Ìï®Ïàò
      // URL.createObjectURL() Í∞ÄÏÉÅÏúºÎ°ú Ìò∏Ïä§ÌåÖÌï¥ÏÑú Ìï¥Îãπ URLÏùÑ ÏÉùÏÑ±Ìï¥Ï£ºÎäî Ìï®Ïàò
      let url = URL.createObjectURL(file[0]);
      // Ïù¥ÎØ∏ÏßÄ ÌååÏùºÎßå Î∞õÍ≥† Ïã∂ÏùÑ Í≤ΩÏö∞ file[0].typeÏùÑ ÌÜµÌï¥ÏÑú ÌååÏùºÏùò ÌÉÄÏûÖÏùÑ Í≤ÄÏÇ¨ÌïòÎäî Í≥ºÏ†ïÏùÑ Ï∂îÍ∞ÄÌï¥Ïïº Ìï®
      // console.log(url);
      // blob: Binary Large Object ÎùºÎäî ÎúªÏúºÎ°ú Ïù¥ÎØ∏ÏßÄÎ•º  Ïù¥ÏßÑÎ≤ï Ïò§Î∏åÏ†ùÌä∏ ÏïàÏóê Îã¥Í∏¥ Îç∞Ïù¥ÌÑ∞ ÌòïÏãùÏúºÎ°ú Î∞òÌôò
      this.imgUrl = url;
    },
    next() {
      if (this.step <= 2) {
        this.step++;
      }
    },
    publish() {
      let newPost = {
        id: 0,
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: `${this.imgUrl}`,
        likes: 36,
        date: "May 15",
        liked: false,
        content: `${this.textData}`,
        filter: `${this.filterData}`,
      };
      this.contents.unshift(newPost);
      this.step = 0;
      this.textData = '';
      this.filterData = '';
    },
    getTextData(textData) {
      this.textData = textData;
    },
    // now() {
    //   return new Date()
    // }
  },
  // methods Ìï®ÏàòÎäî ÏÇ¨Ïö©Ìï† ÎïåÎßàÎã§ Ïã§ÌñâÎê®
  // computed Ìï®ÏàòÎäî ÏÇ¨Ïö©Ìï¥ÎèÑ Ïã§ÌñâÎêòÏßÄ ÏïäÍ≥† Ï≤òÏùå Ïã§ÌñâÌïòÍ≥† Í∞íÏùÑ Í∞ÑÏßÅÌïòÍ≤å Îê®
  // Î∞©ÎåÄÌïú Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§ÏóêÏÑú ÌïÑÏöîÌïú Îç∞Ïù¥ÌÑ∞Îßå Í∞ÄÏßÄÍ≥†ÏôÄÏÑú Ï†ÄÏû•ÌïòÍ±∞ÎÇò ÌïúÎ≤à Ïó∞ÏÇ∞Îêú Í≤∞Í≥ºÎ•º Ï†ÄÏû•ÌïòÎäîÎç∞ Ïì∞ÏûÑ

  // Í∞íÏù¥ Î∞îÎÄîÎïåÎßàÎã§ Ïã§ÌñâÌïòÍ≥† Îã§Ïãú Ïã§ÌñâÌïòÏßÄ ÏïäÏùå
  // Í≥ÑÏÇ∞ Í≤∞Í≥ºÎ•º Ï†ÄÏû•ÌïòÎäî Ìï®ÏàòÎùºÍ≥† ÏÉùÍ∞ÅÌïòÎ©¥ Îê®
  computed : {
    // now2() {
    //   return new Date()
    // }
    // storeÏóê ÏûàÎäî ÎÇ¥Ïö©ÏùÑ Ï∂ïÏïΩÌïòÏó¨ Ïì∞ÎäîÎç∞ÎèÑ Ïì∞ÏûÑ
    name() {
      // computedÎäî returnÏùÑ Î∞òÎìúÏãú Ï†ÅÏñ¥Ï§òÏïºÎê® Í≥ÑÏÇ∞ Í≤∞Í≥ºÎ•º Î±âÍ∏∞ ÎïåÎ¨∏Ïóê
      return this.$store.state.name;
    },
    // storeÏóê ÎÇ¥Ïö©ÏùÑ ÌïòÎÇòÌïòÎÇò computedÌïòÍ∏∞ ÌûòÎìúÎãàÍπå ÏïÑÎûòÏôÄ Í∞ôÏù¥ ÎßåÎì¶
    ...mapState(['name', 'age', 'contents']),
    // Îã§Î•∏ Ïù¥Î¶ÑÏúºÎ°ú Ïì∞Í≥† Ïã∂ÏùÑ Í≤ΩÏö∞ ÏûëÎ™ÖÌïòÍ∏∞
    // ...mapState({ name : 'name'})

  },
  mounted() {
    // mitt ÏÇ¨Ïö©Î≤ï
    // this.emitter.on('EventName', (a) => {
    // aÎäî Ïù¥Î≤§Ìä∏Ïóê Ìè¨Ìï®ÎêòÎäî Îç∞Ïù¥ÌÑ∞Í∞Ä Îì§Ïñ¥ÏûàÎäî Ïò§Î∏åÏ†ùÌä∏
    //   console.log(a)
    // })
    this.emitter.on('selectedFilter', (data) => {
      this.filterData = data;
    })
  },
}
