// import { useStore } from 'vuex';

export default {
  name: "MyPage",
  props: {
    one: Number,
  },
  // composition API ÏÇ¨Ïö©ÌïòÎäî Î∞©Î≤ï
  // setup() ÏùÄ created hookÍ≥º ÎπÑÏä∑
  // props ÏÇ¨Ïö©ÌïòÎäî Î∞©Î≤ï, context Îäî errors Í∞ôÏùÄ Í∏∞ÌÉÄ ÎÇ¥Ïö© ÏÇ¨Ïö©
  // setup(props, context) {
  setup() {
    // Í±∞Ïùò Î™®Îì† Í∏∞Îä• Í∞úÎ∞úÏùÑ setupÏïàÏóêÏÑú Ìï† Ïàò ÏûàÏùå
    // Í¥ÄÎ†® ÏûàÎäî ÎÇ¥Ïö©ÏùÑ ÌïúÏûêÎ¶¨ÏóêÏÑú Î≥º Ïàò ÏûàÎã§Îäî Ïû•Ï†êÏù¥ ÏûàÏùå
    // data Ï†ÄÏû•ÌïòÍ≥† Ïã∂ÏúºÎ©¥ ÏïÑÎûòÏôÄ Í∞ôÏù¥ ÏÇ¨Ïö©
    // ref() ÏïàÏóê ÎÑ£Ïñ¥Ïïº Ìï® Î†àÌçºÎü∞Ïä§Î•º ÎßåÎì†Îã§Îäî Îúª
    // array = array Îäî Í∞í Î∞îÍøîÏ£ºÏÑ∏ÏöîÍ∞Ä ÏïÑÎãàÎùº Í∞íÏùÑ 'Í≥µÏú†Ìï¥Ï£ºÏÑ∏Ïöî'
    // Ïù¥Îü∞ ÌäπÏÑ±ÏùÑ Ïù¥Ïö©Ìï¥ÏÑú Í∞íÏùÑ Í≥µÏú†ÌïòÍ∏∞ ÏúÑÌï¥ ref()Î•º ÏÇ¨Ïö©
    let follower = ref([]);
    let followerOrg = ref([]);


    // { follower Îç∞Ïù¥ÌÑ∞ } ÌòïÏãùÏúºÎ°ú ÎêòÏñ¥ÏûàÏùå

    // Ïó¨Îü¨ props Î•º import Ìï†ÎñÑÎäî toRefsÎùºÎäî Ìï®ÏàòÍ∞Ä ÌïÑÏöî
    // reactive ÌïòÍ≤å ÌïòÍ∏∞ ÏúÑÌï¥ÏÑú Ref ÏïàÏóê ÎÑ£Ïñ¥Ïïº Ìï®
    // let { one } = toRefs(props);
    // console.log(one.value);

    // setup ÏïàÏóêÏÑú watch ÏÇ¨Ïö©ÌïòÎäî Î∞©Î≤ï
    // watch(one, () => {
    //   // one Ïù¥ÎùºÎäî propsÍ∞Ä Î≥ÄÍ≤ΩÎê† ÎïåÎßàÎã§ Ìï®Ïàò Î∞úÎèô
    //   console.log(one.vaule);
    // })

    // computed ÏÇ¨Ïö©ÌïòÎäî Î∞©Î≤ï
    // follower.value.lengthÎäî ÏïÑÎûòÏÑú axios getÏù¥ Ïù¥Î£®Ïñ¥ÏßÄÏßÄ ÏïäÏïòÍ∏∞Ïóê 0ÏúºÎ°ú Ï∂úÎ†•Îê®
    // let result = computed( () => { return follower.value.length });
    // console.log(result.value);


    // let test = reactive({ name: 'kim'})
    // ref()Îäî Ïà´Ïûê, Î¨∏Ïûê, ÎÖºÎ¶¨, reactive() Îäî Array, Object ÏûêÎ£åÌòï ÏßëÏñ¥ÎÑ£Îäî Í≤ΩÏö∞Í∞Ä ÎßéÏùå
    // ÏöîÏ¶òÏùÄ ref()Î°ú Î™®Îëê Ïì∞Í≥† ÏûàÏùå

    // store ÏÇ¨Ïö©ÌïòÎäî Î∞©Î≤ï
    // let store = useStore();
    // console.log(store.state.name);
    // console.log(store.commit());
    // mapState ÏÇ¨Ïö© Î™ªÌï® vuex 5Î≤ÑÏ†Ñ Ïù¥ÏÉÅÏù¥Î©¥ ÏÇ¨Ïö©Îê† ÎìØ


    // mounted ÎùºÏù¥ÌîÑ ÏÇ¨Ïù¥ÌÅ¥ ÌõÖÏùÑ ÏÇ¨Ïö©ÌïòÍ≥† Ïã∂ÏùÑ Í≤ΩÏö∞ onMounted(() => {}) Î¨∏Î≤ïÏùÑ ÏÇ¨Ïö©ÌïòÍ∏∞
    onMounted(() => {

      // ref() ÏïàÏóê ÎÇ¥Ïö© ÏÇ¨Ïö©ÌïòÎäî Î∞©Î≤ï
      axios.get('/follow.json').then((a) => {
        // Îç∞Ïù¥ÌÑ∞ Ï°∞ÏûëÏùÑ ÏúÑÌï¥ÏÑúÎäî .valueÎ•º Î∂ôÏó¨Ïïº ÌïúÎã§.
        follower.value = a.data;
        // ÏõêÎûò Îç∞Ïù¥ÌÑ∞Ïùò ÏÇ¨Î≥∏ÏùÑ followOrgÏóê ÎÑ£Í∏∞
        followerOrg.value = [...a.data];
      })
    });

    // Ìï®Ïàò ÏÇ¨Ïö©ÌïòÎäî Î∞©Î≤ï
    function doSearch(payload) {
      // filter() Ìï®Ïàò ÏûêÏ≤¥Í∞Ä array Îí§Ïóê Î∂ôÍ∏∞ ÎïåÎ¨∏Ïóê Î≥ÑÎèÑÏùò Î∞òÎ≥µÎ¨∏ÏùÑ Ïì∏ ÌïÑÏöîÍ∞Ä ÏóÜÏùå
      let newFollower = followerOrg.value.filter((a) => {
        // Ï∞æÎäî Î¨∏ÏûêÏó¥Ïù¥ ÏóÜÏùÑ Í≤ΩÏö∞ -1ÏùÑ Î¶¨ÌÑ¥ÌïòÍ∏∞ ÎïåÎ¨∏Ïóê -1Ïù¥ ÏïÑÎãåÍ≤ÉÏùÑ Ï∞æÏùå
        return a.name.indexOf(payload) != -1
      });
      follower.value = [...newFollower]
    }
    // return { doThis, follower }
    return { follower, doSearch };
  }
  // created: {
  //   getData() {
  //     axios.get('../../public/follow.json').then( data => {
  //       console.log(data.data);
  //       this.$store.commit('followerList', data.data);
  //     })
  //   }
  // }
}
</script>

