import {createStore} from 'vuex'
import InstaContents from "@/assets/InstaContents";
import axios from 'axios';

// Create a new store instance.
const store = createStore({
    state() {
        return {
            count: 0,
            name: 'kim',
            age: 20,
            contents: InstaContents,

            // actionsÎ°ú ÎçîÎ≥¥Í∏∞Ïóê Îì§Ïñ¥Ïò§Îäî Í∞íÏùÑ ÎÑ£Í∏∞
            more: {},
            follower: [],
        }
    },
    getters: {
        name(state) {
            return state.name;
        },
        age(state) {
            return state.age;
        }
    },
    mutations: {
        increment(state) {
            if(state.count < 1) {
                state.count++
            }else {
                state.count = 0;
            }
        },
        setName(state, payload) {
            state.name = payload;
        },
        addPost(state) {
            state.contents.push(state.more);
        },
        incAge(state) {
            state.age ++
        },
        clkLike(state, payload) {
            if(state.contents[payload].liked === false) {
                state.contents[payload].liked = true;
                state.contents[payload].likes ++;
            }else{
                state.contents[payload].liked = false;
                state.contents[payload].likes --;
            }
        },
        setMore(state, payload) {
            state.more = payload
        },
        followerList(state, payload) {
            state.follower = payload;
        }
    },
    actions: {
        // ajax ÏöîÏ≤≠ ÌïòÎäî Í≥≥ ÎòêÎäî Ïò§Îûò Í±∏Î¶¨Îäî ÏûëÏóÖÎì§
        // ÏàúÏ∞®Ï†ÅÏúºÎ°ú stateÎ≥ÄÍ≤Ω ÌïòÍ≥†Ïã∂ÏúºÎ©¥
        // name Î∞îÍæ∏Îäî mutations Ìï®Ïàò() -> age Î∞îÍæ∏Îäî mutations Ìï®Ïàò()
        // ÏúÑÏôÄ Í∞ôÏùÄ Í≤ΩÏö∞ actionsÎ•º ÏÇ¨Ïö©Ìï¥Ïïº ÌïúÎã§.
        getData(context) {
            axios.get(`https://codingapple1.github.io/vue/more${context.state.count}.json`).then( a => {
                // console.log(a.data);
                // stateÎ•º Î≥ÄÍ≤ΩÌï†ÎïåÎäî Î¨¥Ï°∞Í±¥ mutationÏùÑ Ïù¥Ïö©Ìï¥ÏïºÌï®
                // contextÎäî storeÏùò dataÎ•º ÏùòÎØ∏ÌïòÎäî ÌååÎùºÎØ∏ÌÑ∞Î™Ö
                context.commit('setMore', a.data);
                context.commit('addPost')
                context.commit('increment');
            })
        }


    }
})

export default store;